import React, { Component } from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components';
import Reitg from './Reitg';
import Clock from './Clock';
import Crew from './Crew';

const HR = styled.hr`
  margin: 1px;
`;
const H2 = styled.h2`
  color: white;
  float: left;
  margin-left: 50px;
  margin-top: 8px;
`;
const H3 = styled.h3`
  color: white;
  margin: -1px;
  float: left;
  width: 100%;
`;
const H4 = styled.h4`
  color: #19b5c4;
  margin: -1px;
  float: left;
  width: 100%;
  font-size: 13px;
`;
const SCRN = styled.div`
  background: url(http://kshisa.ru/images/butt/TV01.png) no-repeat;
  background-size: contain;
  height: 305px;
  margin-left: 14px;
`;
const POST = styled.img`
  float: left;
  width: 170px;
  margin-left: 17px;
  margin-top: 15px;
`;
const FOTO = styled.div`
  float: left;
  width: 360px;
  margin-top: 28px;
  margin-left: -1px;
`;
const POLR = styled.div`
  position: relative;
  width: 90%;
  margin: auto;
  color: black;
  font-size: 12px;
  margin-top: 4px;
  background: url(http://kshisa.ru/images/butt/board.png) no-repeat;
  height: 245px;
  background-size: cover;
`;
const INNER = styled.div`
  position: relative;
  width: 80%;
  margin: auto;
  top: 27px;
`;
const IMG = styled.img`
  width: 170px;
  margin-left: 10px;
`;
const BILL = styled.img`
  width: 58px;
  margin-left: 4px;
  margin-top: 7px;
  margin-bottom: 4px;
`;
const BUTT = styled.img`
  margin-left: 10px;
`;
class Card extends Component {
  constructor(props) {
    super(props)
    this.state = {ButNumbl: 0, ButNumbr: 0, ButNumbd: 0}
    this.buttClickc = this.buttClickc.bind(this);
    this.buttClickg = this.buttClickg.bind(this);
    this.buttClicke = this.buttClicke.bind(this);
    this.buttClickw = this.buttClickw.bind(this);
    this.buttClickr = this.buttClickr.bind(this);
    this.buttClickd = this.buttClickd.bind(this);
  }
  buttClickc = () => {
    this.setState(state => ({
      ButNumbl: 0
    }));
  }
  buttClickg = () => {
    this.setState(state => ({
      ButNumbl: 1
    }))
  }
  buttClicke = () => {
    this.setState(state => ({
      ButNumbd: 0
    }))   
  }
  buttClickw = () => {
    this.setState(state => ({
      ButNumbd: 1
    }))   
  } 
  buttClickr = () => {
    this.setState(state => ({
      ButNumbr: 0
    })) 
  }
  buttClickd = () => {
    this.setState(state => ({
      ButNumbr: 1
    }))   
  }

	render () {
    const { info }  = this.props
    const { butsc } = this.props
    const { butsg } = this.props
    let butsl, butsr, butsm
    if (this.state.ButNumbl == 0) { butsl = butsc }     else { butsl = butsg }
    if (this.state.ButNumbr == 0) { butsr = <Reitg /> } else { butsr = <Clock /> }
    if (this.state.ButNumbd == 0) { butsm = <Crew />  } else { butsm = info[10] }
		return (
		  <div>
		    <SCRN>
			    <POST
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm0.jpg'}
            alt=''
          />
			    <FOTO>
			    <IMG
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm1.jpg'}
            alt=''
          />
			    <IMG
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm2.jpg'}
            alt=''
          />
			    <IMG
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm3.jpg'}
            alt=''
          />
			    <IMG
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm4.jpg'}
            alt=''
          />
        </FOTO>
        <H3>{ info[2] + '  ('+info[4] + ')' }</H3>
        <H4>{ info[1] }</H4>        
			</SCRN>
      <H3>
        {butsl.map(but => (
            <BILL
              src={'http://kshisa.ru/images/bill/'+ but +'.png'}
              alt=''
            />
        ))}
        {butsr}
      </H3>
          <BUTT
            src={'http://kshisa.ru/images/bill/butsc.png'}
            alt=''
            onClick={() => this.buttClickc()}
          />
          <BUTT
            src={'http://kshisa.ru/images/bill/butsg.png'}
            alt=''
            onClick={() => this.buttClickg()}
          />
          <BUTT
            src={'http://kshisa.ru/images/butt/crew.png'}
            alt=''
            onClick={() => this.buttClicke()}
          />
          <BUTT
            src={'http://kshisa.ru/images/butt/rew.png'}
            alt=''
            onClick={() => this.buttClickw()}
          />
          <BUTT
            src={'http://kshisa.ru/images/bill/butsr.png'}
            alt=''
            onClick={() => this.buttClickr()}
          />
          <BUTT
            src={'http://kshisa.ru/images/bill/butsd.png'}
            alt=''
            onClick={() => this.buttClickd()}
          />
        <POLR>
          <INNER>
            {butsm}
          </INNER>
        </POLR>      
   	 </div>
		)
	}
}
const mapStateToProps = (state) => ({
  info: state.post.info,
  butsc: state.post.butsc,
  butsg: state.post.butsg
})
export default connect(mapStateToProps)(Card)