import {
  GET_POSTERS,
  GET_FILMDATA,
  UPD_COUNT,
  UPD_COUNTR,
  GET_NEXT,
  GET_NEXTR
} from '../constants'

const initialState = {
  info:     '',
  crew:     [],
  butsc:    [],
  butsg:    [],
  posters:  [],
  animate:  [],
  numbs:    0,
  numba:    0,
  trigger:  0,
  triggerr: 0,
  count:    0,
  countr:   0
}
export default function (state = initialState, action) {
  switch (action.type) {
    case GET_POSTERS:
      return {
        ...state,
        numbs:   action.payload[0],
        posters: action.payload[1],
        numba:   action.payload[2],
        animate: action.payload[3]
    }
    case GET_FILMDATA:
      return {
        ...state,
        info:  action.payload,
        butsc: action.payload[6],
        butsg: action.payload[7],        
        crew:  action.payload[8]
      }
    case UPD_COUNT:
      return {
        ...state,
        count: action.payload
    }
    case UPD_COUNTR:
      return {
        ...state,
        countr: action.payload
    }
    case GET_NEXT:
      return {
        ...state,
        posters: action.payload,
        numbs:   action.numbs,
        trigger: action.trigger
    }
    case GET_NEXTR:
      return {
        ...state,
        posters: action.payload,
        numbs:   action.numba,
        trigger: action.triggerr
    }
    default:
			return state
  }
}
