import React, { Component } from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components';

const Line = styled.div`
  color: azure;
  float: right;
  width: 265px;
`;
const H4 = styled.h4`
  color: white;
  margin: 1px;
  float: left;
  width: 100%;
`;
class Clock extends React.Component {
    constructor(props) {
      super(props);
      this.state = {date0: new Date(),
                    date1: new Date()};
    }
    componentDidMount() {
      this.timerID = setInterval(
        () => this.tick(),
        1000
      );
    }
    componentWillUnmount() {
      clearInterval(this.timerID);
    }
    tick() {
      var d = new Date();
      var hours = (this.props.info[5]-this.props.info[5]%60)/60
      var min = this.props.info[5]%60
      d.setHours(d.getHours() + hours);
      d.setMinutes(d.getMinutes() + min);
      this.setState({
        date0: new Date(),
        date1: d
      });
    }
    render() {
      const { info } = this.props
      return (
        <Line>
          <H4>
          {this.state.date0.toLocaleTimeString()}
          </H4>
          <H4>
          {(info[5]-info[5]%60)/60+':'+info[5]%60}
          </H4>
          <H4>
          {this.state.date1.toLocaleTimeString()}
          </H4>
        </Line>
      )
    }
  }
  const mapStateToProps = (state) => ({
	info: state.post.info
  })
  export default connect(mapStateToProps)(Clock)